class CardsGiver{
    //   (!)  fullDeck    inputDeck  , 
    //  inputDeck    6 
    static giveUpToSixCards(fullDeck, inputDeck){
        //       ,    :
        while(inputDeck.length < 6 && fullDeck.length > 0){
            let card = fullDeck.shift();
            inputDeck.push(card);
        }
    }
}

export default CardsGiver;


